import React, { useEffect, useState } from "react";
import { Card, Col, Row, Tab, Nav } from "react-bootstrap";
import PatientAppointmentNotes from "./PatientAppointmentNotes";
import { IoIosArrowForward } from "react-icons/io";
import GenericPatientCard from "../../../components/Theme/GenericPatientCard";
import { getPatient } from "../../../app/features/patient/patient.slice";
import { Link, useParams } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { getAllAppointment } from "../../../app/features/appointment/appointment.slice";
import { getAllPatientNotes } from "../../../app/features/patientNotes/patientNotes.slice";
import moment from "moment";
import { PATH } from "../../../utils/path";
import OnlinePrescForm from "./Prescription";

export default function PatientNotes() {

  const param = useParams();
  const dispatch = useDispatch();
  const { getPatientData } = useSelector((state) => state.patient);
  const { allAppointment } = useSelector((state) => state.appointment);
  const { user } = useSelector((state) => state.auth);
  const [selectedOptions, setSelectedOptions] = useState(null);
  const [selectedDate, setSelectedDate] = useState(null);

  useEffect(() => {
    const finalData = {
      patientId: param?.patientId,
    };

    dispatch(getPatient(finalData));
  }, [dispatch, param?.patientId]);

  useEffect(() => {
    const finalData = {
      patientId: +param?.patientId,
      doctorId: user?.userId,
      date: selectedDate ? moment(selectedDate).format("YYYY-MM-DD") : null,
      statusId:
        selectedOptions === "205"
          ? 205
          : selectedOptions === "204"
          ? 204
          : selectedOptions === "206"
          ? 206
          : selectedOptions === "203"
          ? 203
          : selectedOptions === "201"
          ? 201
          : selectedOptions === "202"
          ? 202
          : null,
    };
    dispatch(getAllAppointment(finalData));
  }, [dispatch, param?.patientId, user?.userId, selectedDate, selectedOptions]);

  useEffect(() => {
    const finalData = {
      patientId: +param?.patientId,
      doctorId: user?.userId,
      noteTypeId: 1,
    };
    dispatch(getAllPatientNotes(finalData));
  }, [dispatch, param?.patientId, user?.userId]);


  return (
    <>
      <nav aria-label="breadcrumb">
        <ol className="breadcrumb">
          <li className="breadcrumb-item">
            <Link
              to={PATH.PATIENTS}
              className="text-decoration-none fs-5"
              style={{ color: "#999999" }}
            >
              Patients
            </Link>
          </li>
          <IoIosArrowForward
            style={{ color: "#999999" }}
            className="ms-2 mt-2"
          />
          <li
            className="breadcrumb-item active fs-5"
            aria-current="page"
            style={{ color: "#000071" }}
          >{`${getPatientData?.firstName} ${getPatientData?.lastName}`}</li>
        </ol>
      </nav>
      <Row className="my-3 patient-details">
        <Col xl={3} lg={4} md={4}>
          <div className="shadow-sm">
            <GenericPatientCard
              name={
                getPatientData?.firstName
                  ? `${getPatientData?.firstName} ${getPatientData?.lastName}`
                  : "N/A"
              }
              genderInfo="true"
              gender={getPatientData?.gender ? getPatientData?.gender : "N/A"}
              dobInfo="true"
              dob={
                getPatientData?.dob ? getPatientData?.dob.split("T")[0] : "N/A"
              }
              locationInfo="true"
              location={
                getPatientData?.currentAddress
                  ? getPatientData?.currentAddress
                  : "N/A"
              }
              currentLocationInfo="true"
              currentLocation={
                getPatientData?.currentAddress
                  ? getPatientData?.currentAddress
                  : "N/A"
              }
              patientLocalGPInfo="true"
              patientLocalGP={
                getPatientData?.patientLocalGP
                  ? getPatientData?.patientLocalGP
                  : "N/A"
              }
              emailInfo="true"
              email={getPatientData?.email ? getPatientData?.email : "N/A"}
              pharmacyInfo="true"
              pharmacy="Clinix"
              phoneInfo="true"
              phone={
                getPatientData?.phoneNumber
                  ? getPatientData?.phoneNumber
                  : "N/A"
              }
              image={getPatientData?.imageUrl}
            />
          </div>
        </Col>
        <Col xl={9} lg={8} md={8}>
          <Card className="shadow-sm px-4 py-4 h-100">
            <div className="py-3">
              <div></div>
              <h5 className="px-2 py-2 fw-bold header-border">
                Clinical Notes
              </h5>
            </div>
            <Tab.Container id="left-tabs-example" defaultActiveKey="first">
              <Row className="">
                <Col lg={12} md={12}>
                  <Nav variant="pills" className="flex-row">
                  <Nav.Item className="p-2 ps-0">
                      <Nav.Link eventKey="first">Appointment Notes</Nav.Link>
                    </Nav.Item>
                    <Nav.Item className="p-2 ps-0">
                      <Nav.Link eventKey="second">Online Prescriptions</Nav.Link>
                    </Nav.Item>
                    
                  </Nav>
                </Col>
                <Col lg={12} md={12}>
                  <Tab.Content>
                  <Tab.Pane eventKey="first">
                      <PatientAppointmentNotes
                        allAppointment={allAppointment}
                        selectedDate={selectedDate}
                        setSelectedDate={setSelectedDate}
                        selectedOptions={selectedOptions}
                        setSelectedOptions={setSelectedOptions}
                      />
                    </Tab.Pane>
                    <Tab.Pane eventKey="second">
                      <OnlinePrescForm />
                    </Tab.Pane>
                    
                  </Tab.Content>
                </Col>
              </Row>
            </Tab.Container>
          </Card>
        </Col>
      </Row>
    </>
  );
}
