import React from "react";
import { Badge, Card } from "react-bootstrap";
import { AiFillEye } from "react-icons/ai";
import { Link } from "react-router-dom";
import BootstrapTable from "react-bootstrap-table-next";
import paginationFactory from "react-bootstrap-table2-paginator";
import ReactDatePicker from "react-datepicker";
import { BsCalendarFill } from "react-icons/bs";
import "react-datepicker/dist/react-datepicker.css";

export default function PatientAppointmentNotes(props) {
  const { allAppointment, selectedDate, setSelectedDate } = props;
  
  const renderBadge = (status) => {
    return status === 204 ? (
      <Badge pill bg="info" className="upcoming-badge">
        Upcoming
      </Badge>
    ) : status === 201 ? (
      <Badge pill bg="primary" className="pending-badge">
        Pending
      </Badge>
    ) : status === 205 ? (
      <Badge pill bg="danger" className="missed-badge">
        Missed
      </Badge>
    ) : status === 206 ? (
      <Badge pill bg="light" className="completed-badge">
        Completed
      </Badge>
    ) : status === 203 ? (
      <Badge pill bg="warning" className="cancelled-badge">
        Cancelled
      </Badge>
    ) : (
      <Badge>Booked</Badge>
    );
  };

  const columns = [
    {
      dataField: "appointmentId",
      text: "SR.",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
    {
      dataField: "patientName",
      text: "Patients",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
    {
      dataField: "appointmentDate",
      text: "Date",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1" },
      formatter: (text) => (text ? text?.split("T")[0] : "N/A"),
    },
    {
      dataField: "appointmentStartTime",
      text: "Start Time",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
    {
      dataField: "status",
      text: "Status",
      sort: false,
      formatter: (text, row) => renderBadge(row?.statusId),
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
    {
      dataField: "action",
      text: "",
      sort: false,
      formatter: (text, row) => (
        <Link
          to={`/doctor/patients/${row?.patientId}/patient-details/${row?.appointmentId}/appointment-notes`}
          className="table-action"
        >
          <AiFillEye /> View
        </Link>
      ),
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
  ];

  const customTotal = (from, to, size) => (
    <span className="react-bootstrap-table-pagination-total">
      <b> {from} </b> to <b>{to}</b> out of <b>{size} entries</b>
    </span>
  );

  const paginateOptions = {
    paginationSize: 10,
    pageStartIndex: 1,
    alwaysShowAllBtns: true,
    withFirstAndLast: false,
    hideSizePerPage: true,
    showTotal: true,
    paginationTotalRenderer: customTotal,
    disablePageTitle: true,
    sizePerPageList: [
      {
        text: "10",
        value: 10,
      },
    ],
  };

  return (
    <>
      <Card className="  patient-details-card">
        <div className="px-4 py-3 d-flex justify-content-between custom-row align-items-center table-header-border">
          <div>
            <h5 className="mb-0 fw-bold">Appointments</h5>
          </div>
          <div className="d-flex custom-div-row">
            <div className="px-1 d-flex">
              <ReactDatePicker
                selected={selectedDate}
                onChange={(date) => setSelectedDate(date)}
                dateFormat="dd MMM yyyy"
                placeholderText="Date"
                className="custom-field-picker dr-date-w px-3"
              />
              <BsCalendarFill className="custom-date-icon" size={18} />
            </div>
          </div>
        </div>
        <>
          <span className="doctor-datatable apt-badge">
            <BootstrapTable
              columns={columns}
              data={allAppointment.filter(
                (appointment) => appointment.statusId === 206
              )}
              keyField="id"
              id="bar"
              pagination={paginationFactory(paginateOptions)}
              bordered={false}
              wrapperClasses="table-responsive"
            />
          </span>
        </>
      </Card>
    </>
  );
}
