import React, { useEffect, useRef, useState } from "react";
import { Card } from "react-bootstrap";
import BootstrapTable from "react-bootstrap-table-next";
import paginationFactory from "react-bootstrap-table2-paginator";
import { AiFillEye } from "react-icons/ai";
import { useDispatch, useSelector } from "react-redux";
import { getPrescriptionList } from "../../../../app/features/patientPrescription/patientPrescription.slice";
import MedicationModel from "./MedicationModel";
import PrintComponents from "react-print-components";
import { PiDownloadSimpleBold } from "react-icons/pi";
import PrescriptionPDF from "../../../../components/Theme/PrescriptionPDF";
import { useParams } from "react-router-dom";

export default function OnlinePrescForm() {

  const [show, setShow] = useState(false);
  const [viewPresctionData, setViewPresctionData] = useState();
  const printButtonRef = useRef(null);
  const param = useParams();

  const dispatch = useDispatch();

  const { user } = useSelector((state) => state.auth);
  const { allPrescription } = useSelector(
    (state) => state?.patientPrescription
  );

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  const printTimeOut = (data) => {
    setShow(false);
    setTimeout(function () {
      printButtonRef?.current?.click();
    }, 1000);
    // setSelectedDate(viewPresctionData?.medicineList);
  };
  const viewPrescription = (data) => {
    handleShow();
    setViewPresctionData(data);
  };

  const columns = [
    {
      dataField: "prescriptionId",
      text: "ID",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
    {
      dataField: "medicine",
      text: "Medication",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1" },
      formatter: (text) => (text ? text : "N/A"),
    },
    {
      dataField: "allergies",
      text: "Allergy",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1" },
      formatter: (text) => (text ? text : "N/A"),
    },
    {
      dataField: "quantity",
      text: "Quantity",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1" },
      formatter: (text) => (text ? text : "N/A"),
    },
    {
      dataField: "action",
      text: "Action",
      sort: false,
      formatter: (row, data) => (
        <>
          <AiFillEye
            style={{ color: "blue", cursor: "pointer" }}
            onClick={() => viewPrescription(data)}
          />
        </>
      ),
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
  ];

  const customTotal = (from, to, size) => (
    <span className="react-bootstrap-table-pagination-total">
      <b> {from} </b> to <b>{to}</b> out of <b>{size} entries</b>
    </span>
  );

  useEffect(() => {
    const finalData = {
      patientId: +param?.patientId,
      doctorId: user?.userId,
      isOnlineForm: true,
    };
    dispatch(getPrescriptionList({ finalData }));
  }, [dispatch, user?.userId, param?.patientId]);

  const paginateOptions = {
    paginationSize: 8,
    pageStartIndex: 1,
    alwaysShowAllBtns: true,
    withFirstAndLast: false,
    hideSizePerPage: true,
    showTotal: true,
    paginationTotalRenderer: customTotal,
    disablePageTitle: true,
    sizePerPageList: [
      {
        text: "6",
        value: 10,
      },
    ],
  };

  return (
    <>
      <Card className="patient-clinical-note">
        <Card.Header className=" px-4 py-4 table-header-border">
          <h5 className="mb-0 fw-bold">Online Prescriptions</h5>
        </Card.Header>
        <Card.Body
          className="table-body-border p-0"
          style={{ overflow: "auto", height: "695px" }}
        >
          <span className="doctor-datatable">
            <BootstrapTable
              columns={columns}
              data={allPrescription ? allPrescription : []}
              keyField="id"
              id="bar"
              pagination={paginationFactory(paginateOptions)}
              // striped
              bordered={false}
              wrapperClasses="table-responsive"
            />
          </span>
        </Card.Body>
      </Card>
      <MedicationModel
        viewPresctionData={viewPresctionData}
        handleClose={handleClose}
        show={show}
        onDownload={printTimeOut}
      />
      <PrintComponents
        trigger={
          <button
            ref={printButtonRef}
            className="border-0 px-3 py-2 rounded-2 d-none"
          >
            <PiDownloadSimpleBold size={20} /> Download PDF
          </button>
        }
      >
        <PrescriptionPDF prescriptionData={viewPresctionData} />
      </PrintComponents>
    </>
  );
}
